<template>
  <div id="edit-note">
    <head-top title="编辑笔记">
      <span slot="right" @click="saveData">
        <i class="iconfont iconfinish"></i>
      </span>
    </head-top>

    <div class="main">
      <div class="tt">
        <input type="text" placeholder="编辑笔记标题" v-model="title" />
      </div>
      <div class="content" v-show="contentType === contentTypeDefault || !content">
        <textarea placeholder="编辑笔记内容..." v-model="content"></textarea>
      </div>
      <div class="content" v-show="contentType !== contentTypeDefault && content">
        <img :src="imgBase" />
      </div>
      <div class="footer" v-show="contentType !== contentTypeDefault || !content">
        <div class="pic" @click="selectPhoto(1)">
          <!-- <van-uploader preview-image :before-read="beforeRead" :after-read="afterRead"> -->
          <div class="icon">
            <i class="iconfont icontupian"></i>
          </div>
          <div class="text">图片</div>
          <!-- </van-uploader> -->
        </div>
        <div class="line"></div>
        <div class="pho" @click="selectPhoto(2)">
          <!-- <van-uploader
            capture="camera"
            preview-image
            :before-read="beforeRead"
            :after-read="afterRead"
          >-->
          <div class="icon">
            <i class="iconfont iconphotograph"></i>
          </div>
          <div class="text">拍照</div>
          <!-- </van-uploader> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Uploader } from "vant";

import { ContentType, ModulePicture } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: { headTop, [Uploader.name]: Uploader },
  props: {},
  data() {
    return {
      title: "",
      contentType: ContentType.Text, //1文本 2图片
      contentTypeDefault: ContentType.Text,
      content: "",
      imgBase: "",
      saveLoading: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      window.uploadImage = imgFile => {
        this.afterRead(imgFile);
      };
    });
  },
  methods: {
    saveData() {
      if (this.saveLoading) return;
      this.saveLoading = true;
      if (!this.title) {
        this.$toast("笔记标题不能为空~");
        this.saveLoading = false;
        return;
      }
      if (!this.content) {
        this.$toast("笔记内容不能为空~");
        this.saveLoading = false;
        return;
      }

      this.$gatewayHttp.note
        .postSaveEdit({
          courseID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID,
          snTitle: this.title,
          snForm: this.contentType,
          snContent: this.content
        })
        .then(res => {
          if (res.Result) {
            this.$toast.success("添加笔记成功！");
            this.$router.push("/note");
          }
          this.saveLoading = false;
        })
        .catch(err => {
          this.$toast("添加失败，请重试");
          this.saveLoading = false;
        });
    },
    selectPhoto(type) {
      if (type === 2) {
        sendToAPP(FuncType.cameraOpen, "", 1, 1);
      } else {
        sendToAPP(FuncType.albumOpen, "", 1, 1);
      }
    },
    // 返回布尔值
    beforeRead(file) {
      if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/jpg"
      ) {
        this.$toast("请上传 jpg 格式图片");
        return false;
      }
      // 超过20M
      if (file.size > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      return true;
    },
    afterRead(file) {
      console.log("接收到的文件：", file);
      // if (
      //   file.type !== "image/jpeg" &&
      //   file.type !== "image/png" &&
      //   file.type !== "image/jpg"
      // ) {
      //   this.$toast("请上传 jpg 格式图片");
      //   return false;
      // }
      // 超过20M
      if (file.length > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      if (file) {
        let reqData = {
          funType: ModulePicture.StudentNote,
          imgBaseString: file
        };
        this.$gatewayHttp.resourse
          .upload(reqData)
          .then(res => {
            if (res.Result) {
              this.contentType = ContentType.Picture;
              this.imgBase = file;
              this.content = res.Data;
            }
          })
          .catch(err => {});
      }
    }
  },
  computed: {
    // imgFile(){
    //   if(this.contentType=== ContentType.Text){
    //     return "";
    //   }else{
    //     return this.imgBase;
    //   }
    // }
  },
  watch: {}
};
</script>

<style lang='less' scoped>
#edit-note {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .iconfinish {
    float: right;
    margin-right: 4vw;
    line-height: 4.3rem;
    color: #fff;
    font-size: 1.5rem;
  }
  .main {
    text-align: left;
    background: #ebf0ef;

    .tt {
      width: 92vw;
      padding-left: 4vw;
      padding-right: 4vw;
      color: #999;
      height: 7vh;
      line-height: 7vh;
      background: #fff;
      input {
        color: #999;
        height: calc(7vh - 2px);
        line-height: calc(7vh - 2px);
        width: 100%;
      }
    }

    .content {
      margin-top: 4vw;
      height: 71vh;
      margin-bottom: 5vw;

      textarea {
        width: 92vw;
        padding: 4vw;
        height: 65vh;
        background: rgba(255, 255, 255, 1);
        color: #999;
        border: none;
      }
      img {
        max-width: 100vw;
        max-height: 80vh;
        text-align: center;
      }
    }

    .footer {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100vw;
      height: 13.3vw;
      text-align: right;
      background: rgba(255, 255, 255, 1);
      display: flex;
      justify-content: space-around;
      color: #999;
      font-size: 1.2rem;
      & > div {
        margin-top: 5px;
      }
      .pic {
        margin-left: 6vw;
      }
      .pho {
        margin-right: 6vw;
      }
      .iconfont {
        font-size: 2.5rem;
      }
      .line {
        width: 1px;
        height: 8vw;
        background: rgba(221, 221, 221, 1);
        margin-top: 10px;
        margin-top: 2.6vw;
      }
    }
  }
}
</style>


